import { useContext, useState } from "react";
import { PAContext } from "../PAPolicy";
import {
  IconButton,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Divider,
} from "@mui/material";
import PersonSearchIcon from "@mui/icons-material/PersonSearch";
import CustomDatePicker from "../../../../../../../../components/DatePicker";
import ModalWithTable from "../../../../../../../../components/ModalWithTable";

export const clientColumn = [
  { field: "entry_client_id", headerName: "ID", width: 130 },
  { field: "fullname", headerName: "First Name", flex: 1 },
  {
    field: "entry_type",
    headerName: "ID Type",
    width: 150,
  },
];

export const agentColumn = [
  { field: "entry_agent_id", headerName: "ID", width: 130 },
  { field: "fullname", headerName: "First Name", flex: 1 },
  {
    field: "entry_type",
    headerName: "ID Type",
    width: 150,
  },
];

const queryKeyClient = "vehicle-policy";
const queryKeyAgent = "agent-policy";

export default function PAPolicyInformation() {
  const {
    state,
    handleInputChange,
    isLoading,
    clientRows,
    setClientRows,
    customInputchange,
    myAxios,
    user,
    agentRows,
    setAgentRows,
    data,
    isAddOrEditMode,
    clientLoading,
    agentLoading,
    computation,
  } = useContext(PAContext);

  const [openClientTableModal, setOpenClientTableModal] = useState(false);
  const [openAgentTableModal, setOpenAgentTableModal] = useState(false);

  return (
    <div>
      <div
        style={{
          display: "grid",
          gap: "20px",
          gridTemplateAreas: `"content1 content2" "content3 content4" "content5 content5"`,
        }}
      >
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "10px",
            gridArea: "content1",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
          }}
        >
          <legend style={{ color: "#065f46" }}>Insurer Information</legend>

          <div style={{ display: "flex", columnGap: "10px" }}>
            <TextField
              disabled={isAddOrEditMode}
              fullWidth
              variant="outlined"
              size="small"
              label="Client ID"
              name="client_id"
              value={state.client_id}
              onChange={handleInputChange}
              InputProps={{
                readOnly: true,
              }}
            />
            <IconButton
              disabled={isAddOrEditMode || clientLoading}
              aria-label="search-client"
              color="secondary"
              onClick={() => setOpenClientTableModal(true)}
            >
              <PersonSearchIcon />
            </IconButton>
          </div>
          <TextField
            disabled={isAddOrEditMode}
            fullWidth
            variant="outlined"
            size="small"
            label="Client Name"
            name="client_name"
            value={state.client_name}
            onChange={handleInputChange}
            InputProps={{
              readOnly: true,
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            fullWidth
            variant="outlined"
            size="small"
            label="Client Address"
            rows={3}
            multiline
            name="client_address"
            value={state.client_address}
            onChange={handleInputChange}
            InputProps={{
              readOnly: true,
            }}
          />
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "10px",
            gridArea: "content2",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
          }}
        >
          <legend style={{ color: "#065f46" }}>Agent Information</legend>

          <div style={{ display: "flex", columnGap: "10px" }}>
            <TextField
              disabled={isAddOrEditMode}
              fullWidth
              variant="outlined"
              size="small"
              label="Agent ID"
              name="agent_id"
              value={state.agent_id}
              onChange={handleInputChange}
              InputProps={{
                readOnly: true,
              }}
            />
            <IconButton
              disabled={isAddOrEditMode || agentLoading}
              aria-label="search-client"
              color="secondary"
              onClick={() => setOpenAgentTableModal(true)}
            >
              <PersonSearchIcon />
            </IconButton>
          </div>
          <TextField
            disabled={isAddOrEditMode}
            fullWidth
            variant="outlined"
            size="small"
            label="Agent Name"
            name="agent_name"
            value={state.agent_name}
            onChange={handleInputChange}
            InputProps={{
              readOnly: true,
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            fullWidth
            variant="outlined"
            size="small"
            label="Commission"
            name="agent_com"
            value={state.agent_com}
            onChange={handleInputChange}
          />
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            marginBottom: "10px",
          }}
        >
          <legend style={{ color: "#065f46" }}>PA Policy</legend>
          <FormControl size="small" fullWidth disabled={isAddOrEditMode}>
            <InputLabel id="PolicyAccount">Account</InputLabel>
            <Select
              labelId="PolicyAccount"
              value={state.PolicyAccount}
              label="Account"
              name="PolicyAccount"
              onChange={handleInputChange}
            >
              {[{ Account: "" }, ...data?.data.paPolicy.policy_account].map(
                (items: any, idx: number) => {
                  return (
                    <MenuItem key={idx} value={items.Account}>
                      {items.Account}
                    </MenuItem>
                  );
                }
              )}
            </Select>
          </FormControl>
          <TextField
            disabled={isAddOrEditMode}
            fullWidth
            variant="outlined"
            size="small"
            label="Policy No"
            name="PolicyNo"
            value={state.PolicyNo}
            onChange={handleInputChange}
          />
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "10px",
            gridArea: "content4",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
          }}
        >
          <legend style={{ color: "#065f46" }}>Period of Insurance</legend>
          <CustomDatePicker
            disabled={isAddOrEditMode}
            label="Date From"
            onChange={(e: any) => {
              customInputchange(e, "DateFrom");
            }}
            value={new Date(state.DateFrom)}
          />
          <CustomDatePicker
            disabled={isAddOrEditMode}
            label="Date To"
            onChange={(e: any) => {
              customInputchange(e, "DateTo");
            }}
            value={new Date(state.DateTo)}
          />
          <CustomDatePicker
            label="Date Issued"
            name="DateIssued"
            onChange={(e: any) => {
              customInputchange(e, "DateIssued");
            }}
            value={new Date(state.DateIssued)}
            disabled={isAddOrEditMode}
          />
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            gap: "20px",
            gridArea: "content5",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
          }}
        >
            <TextField
              style={{ flex: 1 }}
              disabled={isAddOrEditMode}
              variant="outlined"
              size="small"
              label="Property Insured"
              name="propertyInsured"
              value={state.propertyInsured}
              onChange={handleInputChange}
              multiline
              rows={10}
            />
            <fieldset
              style={{
                flex: 1,
                display: "flex",
                flexDirection: "column",
                rowGap: "10px",
                padding: "15px",
                border: "1px solid #cbd5e1",
                borderRadius: "5px",
              }}
            >
              <legend>Premiums</legend>
              <div
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  padding: "5px",
                }}
              >
                <Button
                  disabled={isAddOrEditMode}
                  size="small"
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    computation();
                  }}
                >
                  Compute
                </Button>
              </div>

              <TextField
                disabled={isAddOrEditMode}
                required
                variant="outlined"
                size="small"
                label="Net Premium"
                name="netPremium"
                value={state.netPremium}
                onChange={handleInputChange}
              />
              <TextField
                disabled={isAddOrEditMode}
                required
                variant="outlined"
                size="small"
                label="Vat"
                name="vat"
                value={state.vat}
                onChange={handleInputChange}
              />
              <TextField
                disabled={isAddOrEditMode}
                required
                variant="outlined"
                size="small"
                label="Doc Stamp"
                name="docStamp"
                value={state.docStamp}
                onChange={handleInputChange}
              />
              <div
                style={{ display: "flex", gap: "5px", position: "relative" }}
              >
                <TextField
                  sx={{ width: "80px" }}
                  disabled={isAddOrEditMode}
                  required
                  variant="outlined"
                  size="small"
                  name="localGovTaxPercent"
                  value={state.localGovTaxPercent}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  variant="outlined"
                  size="small"
                  label="Local Gov Tax"
                  name="localGovTax"
                  value={state.localGovTax}
                  onChange={handleInputChange}
                  style={{flex:1}}
                />
              </div>
              <Divider color="secondary" />
              <TextField
                disabled={isAddOrEditMode}
                required
                variant="outlined"
                size="small"
                label="Total Due"
                name="totalDue"
                value={state.totalDue}
                onChange={handleInputChange}
              />
            </fieldset>
        </fieldset>
      </div>
      <ModalWithTable
        showModal={openClientTableModal}
        onCloseModal={() => {
          setOpenClientTableModal(false);
        }}
        onClickCloseIcon={() => {
          setOpenClientTableModal(false);
        }}
        searchOnChange={(e) => {
          myAxios
            .get(
              `/task/production/search-client-vehicle-policy?clientSearch=${e.target.value}`,
              {
                headers: {
                  Authorization: `Bearer ${user?.accessToken}`,
                },
              }
            )
            .then((res: any) => {
              if (!res?.data.success) {
                return alert(`Error : ${res?.data.message}`);
              }
              setClientRows(res?.data.vehiclePolicy?.clients);
            });
        }}
        height={300}
        isLoading={isLoading}
        queryKey={queryKeyClient}
        columns={clientColumn}
        onSelectionChange={(rowSelectionModel, data) => {
          if (rowSelectionModel.length <= 0) {
            return;
          }
          const selectedIDs = new Set(rowSelectionModel);
          const selectedRowData = data.filter((row: any) => {
            return selectedIDs.has(row.entry_client_id.toString());
          });
          if (selectedRowData.length <= 0) return;
          customInputchange(selectedRowData[0].entry_client_id, "client_id");
          customInputchange(selectedRowData[0].fullname, "client_name");
          customInputchange(selectedRowData[0].address, "client_address");
          setOpenClientTableModal(false);
        }}
        id="entry_client_id"
        rows={clientRows}
        setRows={setClientRows}
      />
      <ModalWithTable
        showModal={openAgentTableModal}
        onCloseModal={() => {
          setOpenAgentTableModal(false);
        }}
        onClickCloseIcon={() => {
          setOpenAgentTableModal(false);
        }}
        searchOnChange={(e) => {
          myAxios
            .get(
              `/task/production/search-agent-vehicle-policy?agentSearch=${e.target.value}`,
              {
                headers: {
                  Authorization: `Bearer ${user?.accessToken}`,
                },
              }
            )
            .then((res: any) => {
              if (!res?.data.success) {
                return alert(`Error : ${res?.data.message}`);
              }
              setAgentRows(res?.data.vehiclePolicy?.agents);
            });
        }}
        height={300}
        isLoading={isLoading}
        queryKey={queryKeyAgent}
        columns={agentColumn}
        onSelectionChange={(rowSelectionModel, data) => {
          if (rowSelectionModel.length <= 0) {
            return;
          }
          const selectedIDs = new Set(rowSelectionModel);
          const selectedRowData = data.filter((row: any) => {
            return selectedIDs.has(row.entry_agent_id.toString());
          });
          if (selectedRowData.length <= 0) return;
          customInputchange(selectedRowData[0].entry_agent_id, "agent_id");
          customInputchange(selectedRowData[0].fullname, "agent_name");
          setOpenAgentTableModal(false);
        }}
        id="entry_agent_id"
        rows={agentRows}
        setRows={setAgentRows}
      />
    </div>
  );
}
